<template>
  <div class="add-Account">
    <div class="addform">
      <el-card class="box-card">
        <div slot="header" class="clearfix">
          <span>添加账号</span>
        </div>
        <div class="text item">
          <section class="addlogin">
            <el-form ref="addform" :model="form" :rules="rules" label-width="80px">
              <el-form-item label="账号" prop="name">
                <el-input placeholder="请输入账号" v-model="form.name"></el-input>
              </el-form-item>
              <el-form-item label="密码" prop="pwd">
                <el-input placeholder="请输入密码" v-model="form.pwd" show-password></el-input>
              </el-form-item>
              <el-form-item label="用户组" prop="group">
                <el-select v-model="form.group" placeholder="请选择用户组">
                  <el-option label="超级管理员" value="超级管理员"></el-option>
                  <el-option label="部门管理员" value="部门管理员"></el-option>
                  <el-option label="普通管理员" value="普通管理员"></el-option>
                </el-select>
              </el-form-item>
              <el-form-item class="addbtn">
                <el-button type="primary" @click="onSubmit">添加</el-button>
                <el-button @click="resetForm">重置</el-button>
              </el-form-item>
            </el-form>
          </section>
        </div>
      </el-card>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      form: {
        name: "",
        pwd: "",
        group: "普通管理员"
      },
      rules: {
        name: [
          { required: true, message: "请输入账户名称", trigger: "blur" },
          { min: 2, max: 5, message: "长度在 2 到 5 个字符", trigger: "blur" }
        ],
        pwd: [
          { required: true, message: "请输入活动名称", trigger: "blur" },
          { min: 6, message: "长度在6个字符以上", trigger: "blur" }
        ]
      }
    };
  },
  methods: {
    onSubmit() {
      this.$refs.addform.validate(result => {
        if (result) {
          // console.log(result);
          let xhr = new XMLHttpRequest();
          xhr.open("post", "http://127.0.0.1:3333/users.php?a=add");
          xhr.setRequestHeader(
            "Content-Type",
            "application/x-www-form-urlencoded"
          );
          xhr.send(
            `account=${this.form.name}&password=${this.form.pwd}&userGroup=${this.form.group}`
          );
          xhr.onreadystatechange = () => {
            if (xhr.readyState === 4 && xhr.status === 200) {
              let data = JSON.parse(xhr.responseText);
              // console.log(data);
              if(data.code==0){
                this.$message({
                  message: data.msg,
                  type: "success"
                });
                  this.$router.push('/index/Account-list')
              }else{
                  this.$message.error(data.msg);
              }
            } 
          };
        } else {
          return false;
        }
      });
    },
     resetForm(){
       this.form.name=""
       this.form.pwd=""
       this.form.group="普通管理员"
     }
  }
};
</script>

<style >
.add-Account .addform {
  margin: 0;
  width: 100% !important;
}

.addlogin {
  width: 100%;
  background: #ffffff;
}
.addlogin .el-form {
  width: 300px;
  margin-left: 64px;
}
.addlogin {
  padding: 30px 0 20px;
}
/* .addlogin .addbtn {
  margin-left: -62px !important;
} */
</style>